.page-settings {
    padding: 4rem 0;
    background-color: rgba(0, 0, 0, 0.65);

    .page-settings-header {
        color: #fff;
    }
    .main-body {
        padding: 15px;
    }
    nav {
        background-color: #fff
    }
    nav a {
        margin: 2px 10px;
    }
    .nav-link {
        color: #222;
    }
    .card {
        box-shadow: 0 1px 3px 0 rgba(0,0,0,.1), 0 1px 2px 0 rgba(0,0,0,.06);
    }
    
    .card {
        position: relative;
        display: flex;
        flex-direction: column;
        min-width: 0;
        word-wrap: break-word;
        background-color: #fff;
        background-clip: border-box;
        border: 0 solid rgba(0,0,0,.125);
        border-radius: .25rem;
    }
    
    .card-body {
        flex: 1 1 auto;
        min-height: 1px;
        padding: 1rem;
    }
    
    .gutters-sm {
        margin-right: -8px;
        margin-left: -8px;
    }
    
    .gutters-sm>.col, .gutters-sm>[class*=col-] {
        padding-right: 8px;
        padding-left: 8px;
    }
    .mb-3, .my-3 {
        margin-bottom: 1rem!important;
    }
    
    .bg-gray-300 {
        background-color: #e2e8f0;
    }
    .h-100 {
        height: 100%!important;
    }
    .shadow-none {
        box-shadow: none!important;
    }

    .tab-pane h6 {
        font-weight: bold;
        font-size: larger;
    }
    .form-group > label.d-block {
        font-weight: bold;
    }
    .form-group > input {
        font-weight: bold;
    }
    button {
        color: #fff;
        &:hover {
            background-color: inherit;
            background-color: rgba(0, 0, 0, 0.7);
        }
    }
    button > a {
        color: #fff;
        &:hover {
            text-decoration: none;
        }
    }
    .card {

        .error-body, .success-body {
            height: 2rem;
            display: flex;
            justify-content: center;
            align-items: center;
            color: #530303;
            font-weight: bold;
            background: #faecec;
            margin: 1rem;
        }
        .success-body {
            color: #0e3610;
            background: #c8fac3;
        }
        #seller .seller-des {
            color: #000!important;
            font-size: 16px;
        }
    }

}